.adshome__box {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 50px 50px 10px 50px;
    margin-top: 30px;
    background: #f7f7f7;
    border-radius: 25px;
}

.adshome__content {
    width: 340px;
    height: 340px;
    background: linear-gradient(180deg, rgba(255, 96, 96, 50%), rgba(230, 96, 96, 1) 100%); 
    border-radius: 10px;
    text-decoration: none;
    list-style: none;
    display: flex;
    align-items: flex-end;
    color: #fff;
    position: relative;
    margin: 0 0 40px 0;

}

.adshome__image-cover {
    width: 340px;
    height: 280px;
    object-fit: cover;
    position: absolute;
    top: 0;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.adshome__content p {
    width: 100%;
    font-size: 18px;
    margin: 10px;
}

