.footer{
    background: black;
    color: white;
    width: 100%;
    height:10em;
    display: flex;
    justify-content:center;
    align-items:center;
    flex-direction:column;
    margin: 15px 0;
    border-radius: 3px;
}

.slider__box__slide {
    max-width: 100%;
    height: 415px;
    overflow: hidden;
    position: relative;

}

.slider__box__slide__active-image {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 25px;

}

.slider__box__slide__hidden {
    display: none;
    width: 100%;
    height: 100%;
    border-radius: 25px;
}

.slider__box__slide__left {
    position: absolute;
    top: 40%;
    left: 20px;
    cursor: pointer;
    opacity: 0.5;
}

.slider__box__slide__right {
    position: absolute;
    top: 40%;
    right: 20px;
    cursor: pointer;
    opacity: 0.5;
}

.slider__box__slide__left:hover,
.slider__box__slide__right:hover {
    opacity: 1;
}

.slide__number {
    color: #fff;
    font-size: 18px;
    position: absolute;
    bottom: 20px;
    left: 50%
}


@media(max-width: 768px) {
    .slider__box {
        width: 90%;
        margin: auto;
    }

    .slider__box__slide__left img,
    .slider__box__slide__right img {
        width: 25px;
    }

    .slide__number {
        display: none;
    }
}

@media(max-width:450px){
    .slider__box__slide{
        height:280px
    }
}
.house-details__header {
    width: 100%;
    margin: 15px 0 10px 0;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.house-details__header-left {
    width: 50%;
    display: flex;
    flex-direction: column;
    color: #FF6060;
}

.house-details__header-left h1 {
    margin: 0;
    font-size: 36px;
    font-weight: 500;
}

.house-details__header-left p {
    margin: 5px 0 0 0;
    font-weight: 500;
    font-size: 18px;
}

.house-details__header-tagsbox {
    display: flex;
    flex-direction: row;
    margin: 15px 0 0 0;
    padding: 0;
    flex-wrap: wrap;

}

.house-details__header-tagsbox li {
    color: #fff;
    border-radius: 15px;
    text-align: center;
    background: #ff6060;
    padding: 2px 20px;
    font-size: 14px;
    list-style: none;
    margin: 0 10px 10px 0;
    margin-bottom: 10px;

}

.house-details__header-right {
    display: flex;
    flex-direction: column;
    position: relative;
}

.house-details__header__identification {
    display: flex;
    flex-direction: row;
    position: relative;
}

.house-details__header__identification__name {
    color: #ff6060;
    font-size: 18px;
    font-weight: 500;
    margin-right: 10px;
    position: absolute;
    top: 10px;
    right: 80px;
    /* width:100px; */
    text-align: right;
}

.house-details__header__identification__picture img {
    width: 64px;
    height: 64px;
    border-radius: 50%;
    background: #c4c4c4;
    position: absolute;
    top: 0;
    right: 0;
}

.house-details__header__rating {
    display: flex;
    flex-direction: row;
    margin: 85px 0 0 0;
}

@media(max-width: 768px) {
    .house-details__header {
        flex-direction: column;
        width: 90%;
        margin: auto;
    }

    .house-details__header-right {
        flex-direction: row-reverse;
        justify-content: space-between;
        align-items: center;
        height: 80px;
    }

    .house-details__header__rating {
        margin: 0;
    }

    .house-details__header__identification {
        justify-content: center;
        align-self: start;
    }

    .house-details__header-left {
        width: 100%;
    }

    .house-details__header-left h1 {
        font-size: 30px;
    }

}

@media(max-width:450px) {
    .house-details__header-left h1 {
        font-size: 24px;
    }

    .house-details__header-left p {
        font-size: 16px;
    }
}

@media(max-width:350px) {
    .house-details__header-left h1 {
        font-size: 20px;
    }

    .house-details__header-left p {
        font-size: 14px;
    }

    .house-details__header-tagsbox li {
        font-size: 14px
    }

    .house-details__header__identification__name{
        font-size: 14px
    }

    .house-details__header__rating-image{
        width:10%;
    }
}
.house-details__box {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0 0 30px 0;
}

.house-details__description-box {
    margin-right: 50px;
}

.house-details__description-box,
.house-details__equipment-box {
    width: 50%;
    display: flex;
    flex-direction: column;
}

.house-details__description-box__title,
.house-details__equipment-box__title {
    display: flex;
    justify-content: space-between;
    padding: 10px 15px;
    background: #ff6060;
    border-radius: 10px;
    color: #fff;
    font-size: 18px;
    z-index: 99;
}

.house-details__equipment-box__title i,
.house-details__description-box__title i {
    cursor: pointer;
    font-size: 25px;
}

.house-details__description-box__content {
    display: flex;
    justify-content: space-between;

}

.house-details__equipment-box__list {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    list-style: none;
}

.house-details__description-box__content,
.house-details__equipment-box__list {
    font-size: 18px;
    height: 100%;
    padding: 30px 20px 20px 20px;
    border-radius: 10px;
    color: #ff6060;
    background: #f7f7f7;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    margin: -5px 0 0 0;

}

@media(max-width: 768px) {
    .house-details__box {
        width:90%;
        margin: auto;
        flex-direction: column;
    }

    .house-details__description-box,
    .house-details__equipment-box {
        width: 100%;
    }

    .house-details__description-box{
        margin-bottom: 15px;
    }
}
