.banner__home__box {
    width: 100%;
    margin: auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
    position: relative;
}

.banner__home__header {
    opacity: 1;
    border-radius: 25px;
    width: 100%;
    height: 220px;
    margin: auto;
}

.banner__home__box p {
    text-align: center;
    font-weight: 500;
    font-size: 48px;
    color: #fff;
    position: absolute;
    right: 0;
    width: 100%;
}

@media(max-width: 768px) {
    .banner__home__box {
        width: 90%;
        margin: auto;
    }

    .banner__home__header {
        height: 160px;
        object-fit: cover;
    }

    .banner__home__box p {
        font-size: 24px;
    }
}
